/*CSS reset*/
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.world-box{
    border: 2px solid olivedrab;
    position: relative;
}

.blauw{
    position: absolute;
    left: 607px;
    top: 315px;
    height: 383px;
    transition: height 2s;
}

.blauw:hover{
    height: 480px;
    z-index: 1;
}

.donkerGroen{
    position: absolute;
    left: 42px;
    top: 113px;
    z-index: 1;
    height: 364px;
    transition: height 2s;
}

.donkerGroen:hover{
    height: 464px;

